import React from "react";

export default function SlideEight(props) {
  return (
    <div className="section section--h-full">
      <div className="carousel--item-one">
        <img src="./images/jpg/Hiking.jpg" alt="" />
      </div>
      <div className="section carousel--item-two">
        <h3 className="title">Coding Bootcamp</h3>
        <p className="body">
        While working on my graphic design degree, I tried to work at an
        Amazon Fulfilment center. I hurt my back quickly while working
        there, and they sent me to a doctor that said it looked like I had a
        slipped disc but wouldn't look at it any further. The Amazon Amcare
        then told me to find a new job. Unwilling to accept this, I took a
        month personal leave but after two weeks of being in extreme pain, I
        agreed. I then began to investigate coding bootcamps. I sold my car and
        went to Utah in hopes of better job prospects.

        </p>
      </div>
    </div>
  );
}
