import React from "react";

export default function SlideNine(props) {
  return (
    <div className="section section--h-full">
      <div className="carousel--item-one">
        <img src="./images/jpg/Practice.jpg" alt="" />
      </div>
      <div className="section carousel--item-two">
        <h3 className="title">A Most Difficult Year</h3>
        <p className="body">
        Since leaving the bootcamp, I have worked every day near 10-14
        hours a day on code and development. It has been extremely mentally
        taxing as extreme moments of isolation is difficult for our species. I
        recall when I was studying Buddhism prior to Hesychasm many years ago, that I envied a
        monk that spent six months in isolation in a forest. I don't believe I
        envy that monk any longer. Through the experience I have an unrivaled
        confidence in my ability to code and solve problems.

        </p>
      </div>
    </div>
  );
}
